#Chapters h1 {
	text-align: justify;
}
.accordion {
	margin-top: 3rem;
}
.accordion-item {
	min-width: 330px;
	border-radius: 0.25rem;
	box-shadow: 0px 10px 20px rgba(58, 58, 58, 0);
}

.accordion-header {
	margin: 0;
}
.accordion-body {
	font-family: Georgia, 'Times New Roman', Times, serif;
	font-size: small;
}

.accordion button {
	font-family: Georgia, 'Times New Roman', Times, serif;
	font-size: 1.1rem;
	height: 70px;
	border-radius: 0.25rem;
	border: 0px;
	box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.50) !important;
}

.accordion button:hover,
.accordion button:focus,
.accordion button:active,
.accordion-button:not(.collapsed) {
	color: goldenrod !important;
	background: #404040;
	border-color: transparent;
}

.accordion-button:not(.collapsed)::after {
    background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 16 16' fill='goldenrod'%3e%3cpath fill-rule='evenodd' d='M1.646 4.646a.5.5 0 0 1 .708 0L8 10.293l5.646-5.647a.5.5 0 0 1 .708.708l-6 6a.5.5 0 0 1-.708 0l-6-6a.5.5 0 0 1 0-.708z'/%3e%3c/svg%3e");
}
.accordion-button::after {
    background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 16 16' fill='goldenrod'%3e%3cpath fill-rule='evenodd' d='M1.646 4.646a.5.5 0 0 1 .708 0L8 10.293l5.646-5.647a.5.5 0 0 1 .708.708l-6 6a.5.5 0 0 1-.708 0l-6-6a.5.5 0 0 1 0-.708z'/%3e%3c/svg%3e");
}